.woocommerce-account {

	.woocommerce {
		display: grid;
		grid-template-columns: 2fr 10fr;
		grid-gap: 40px;

		#customer_login {
			grid-column: 1 / -1;
			display: grid;
			grid-template-columns: 1fr;

			@include widerThan($tabletSM) {
				grid-template-columns: 1fr 1fr;
				grid-gap: 150px;
			}

			.col-1,
			.col-2 {
				display: flex;
				flex-direction: column;
				align-items: center;
			}

			.woocommerce-form-login,
			.woocommerce-form-register {
				width: 100%;
				margin: 0;

				.form-row {

					label {
						flex-basis: 30%;
						margin-right: 20px;
						display: flex;
						align-items: center;
					}

					input[type="text"],
					input[type="password"],
					input[type="email"] {
						flex-basis: 70%;
					}

					.woocommerce-form__label-for-checkbox {
						flex-basis: auto;
					}
				}

				.astoundify-recaptcha {
					display: flex;
					justify-content: center;
					padding: 1em;
					background: #f5f5f5;
				}

				.woocommerce-LostPassword {
					text-align: center;
				}

				.generate-password-notice {
					background: #f5f5f5;
					padding: 1.5em;
					text-align: center;
				}

				.mailchimp-newsletter {
					justify-content: center;
				}

				.woocommerce-privacy-policy-text {
					font-size: 13px;
					color: #b3b3b3;

					a {
						font-size: 13px;
						color: #b3b3b3;

						&:hover {
							color: #343533;
						}
					}
				}

				.woocommerce-FormRow {
					flex-direction: column;
				}
			}
		}

		.woocommerce-MyAccount-navigation {

			& > ul {
				list-style: none;
				margin: 0 0 2em;
				padding: 0;
				display: flex;
				flex-direction: column;
				justify-content: flex-start;

				& > li {

					&.is-active {

						a {
							background: $color__accent-hover;

							&:hover {
								background: darken($color__accent-hover, 10%);
							}
						}
					}

					a {
						display: block;
						padding: 1em 2em;
						border-bottom: none;
						box-shadow: none;
						background: #f5f5f5;

						&:hover {
							background: darken(#f5f5f5, 10%);
						}
					}
				}

				.woocommerce-MyAccount-navigation-link--customer-logout {

					a {

						&::before {
							display: none;
							font-family: "Font Awesome 5 Free";
							font-weight: 900;
							content: "\f52b";
							font-style: normal;
							font-variant: normal;
							text-rendering: auto;
						}

						& > svg {
							margin-right: 10px;
						}
					}
				}
			}
		}

		.woocommerce-MyAccount-content {

			.woocommerce-message {

				&[class$="-info"] {
					display: flex;
					flex-direction: column-reverse;
					justify-content: center;
					align-items: center;

					.button {
						margin-top: 10px;
					}
				}
			}

			h3 {
				text-align: left;
			}
		}
	}

	&.woocommerce-orders {

		.woocommerce-MyAccount-content {

			&.no-orders {
				display: flex;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}
		}
	}
}
